<template>
  <div id="login">
    <login></login>
  </div>
</template>

<script>
  import login from '@/components/Login'
    export default {
        name: 'loginApp',
        components: {
          'login': login
        }
    }
</script>

<style scoped>
  #login {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    /*margin-top: 10px;*/
    box-shadow: 0 2px 4px 0 rgba(0,0,0,.05);
  }
</style>



